.waitstaff {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2rem + 2vmin);
  &__section {
    display: flex;
  }
  &__table {
    background-color: white;
    color: black;
    border: 3px solid forestgreen;
    padding: 3rem;
  }

  &__table-head--row {
    text-decoration: underline;
  }
  &__table-name {
    color: red;
    padding: 0 40px;
  }
  &__remove-button {
    font-size: 2rem;
  }
}
